body, html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    background-color: black;
  }
  
  #root {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: flex-end;
  }
  
  .calculator {
    width: 100vw;
    max-width: 500px;
    background-color: black;
    padding: 10px;
    box-sizing: border-box;
  }
  
  .display {
    background-color: black;
    color: white;
    font-size: 4.5em;
    padding: 20px;
    text-align: right;
    width: 100%;
    box-sizing: border-box;
  }
  
  .buttons {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    width: 100%;
    box-sizing: border-box;
  }
  
  button {
    font-size: 2em;
    border: none;
    background-color: #505050;
    color: white;
    cursor: pointer;
    transition: background-color 0.2s;
    border-radius: 50%;
    width: 100%;
    aspect-ratio: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .calculator-icon {
    width: 24px;
    height: 24px;
  }
  
  button.gray {
    background-color: #a5a5a5;
  }
  
  button.orange {
    background-color: #ff9500;
    color: white;
  }
  
  /* Add this media query for mobile devices */
  @media screen and (max-width: 500px) {
    .calculator {
      padding: 8px;
    }
    
    .buttons {
      gap: 8px; /* Slightly reduce gap on mobile */
    }
  }